.overlayTexture {
    position: relative;

    &::after {
        position: absolute;
        top: 0;
        left: 0;
        content: "";
        width: 100vw;
        height: 100%;
    
        z-index: 100;
    
        background-image: url('../../resources/patterns/Meso_White.svg');
        mix-blend-mode: soft-light;
        opacity: .07;
        background-size: 100px;
    
        pointer-events: none;
    }
}