.menu {
    position: absolute;
    z-index: 3;
    color: $color-primary;

    &__icon {
        display: none;
    }

    &__content {
        position: fixed;
        display: flex;
        flex-direction: row;
        align-items: center;
        left: 0;
        height: 70px;
        width: 100vw;
        background-color: $color-dark;
        // padding: 0;

        transition: left 200ms ease-out;
    }

    &__navigation {
        display: flex;
        flex-direction: row;
        justify-content: center;
        margin: 0 auto;
    }

    &__navLink {
        text-transform: uppercase;
        text-decoration: none;
        font-weight: bold;
        font-size: $font-size-medium;
        margin-right: 100px;

        &--selected {
            color: $color-primary;
            font-weight: bold;
        }

        &:last-child {
            margin-right: 0;
        }
    }

    &__links {
        display: none;
        // position: absolute;
        // // margin-right: 200px;
        // right: 200px;
        // top: 15px;
    }

    &__link {
        width: 40px;
        height: 40px;
        font-size: 40px;
        margin-right: 20px;

        &:last-child {
            margin-right: 0;
        }
    }
}

@include media(0, $screen-lg) {
    .menu {
        &__icon {
            display: initial;
            position: absolute;
            left: 20px;
            top: 20px;
            cursor: pointer;
            width: 36px;
            height: 36px;
            font-size: 36px;

            z-index: 1;
        }

        &__content {
            position: absolute;
            flex-direction: column;
            justify-content: center;
            background-color: $color-dark-lighter;
            width: 400px;
            height: 100vh;
            padding: 76px;
            padding-top: 100px;


            &--closed {
                left: -400px;
            }
        }

        &__navigation {
            flex-direction: column;
        }

        &__navLink {
            margin-right: 0;
            margin-bottom: 40px;
            text-align: center;

            &:last-child {
                margin-bottom: 0;
            }
        }

        &__links {
            display: flex;
            margin-top: 100px;
        }
    }
}