.tileContainer {
    display: grid;
    gap: 20px;
    grid-template-columns: repeat(2, 1fr);
    grid-template-areas:
        "a    b"
        "c    c"
        "d    e"
        "f    e"
        "g    h"
    ;
}

.tile {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 0;
    cursor: pointer;

    &:nth-child(4n-1) {
        grid-area: c;
    }

    &:nth-child(7n+5) {
        grid-area: e;

        & .tile__image {
            height: 1000px;
        }
    }

    transition: transform 200ms ease-out;

    &:hover {
        transform-origin: center;
        transform: scale(1.02);
    }

    &__title {
        position: absolute;
        margin-top: 10px;
        text-align: center;
        font-weight: bold;
        background-color: $color-dark-lighter;
        color: $color-primary;
        padding: 4px 14px;
        font-size: $font-size-large;
    }

    &__image {
        width: 100%;
        height: 490px;
        object-fit: cover;
    }
}

@include media(0, $screen-xl) {
    .tileContainer {
        display: flex;
        flex-direction: column;
    }

    .tile {
        margin-bottom: 20px;

        &__image {
            height: calc(100vw - 40px - 17px);
        }
    }
}