.showcaseGroup {
    display: flex;
    margin-top: calc(50vh - (100vh - 200px) / 2);

    &>* {
        margin-right: 100px;

        &:last-child {
            margin-right: 0;
        }
    }
}

.showcase {
    position: relative;
    display: flex;
    justify-content: center;
    background-color: $color-dark-lighter;
    z-index: 1;
    width: fit-content;
    height: fit-content;
    max-width: calc(100vw);
    box-shadow: $shadow;

    &__image {
        width: calc(100vh - 200px);
        height: calc(100vh - 200px);

        object-fit: cover;
    }

    &__index {
        position: absolute;
        top: 20px;
        right: 20px;
        color: $color-primary;
        font-weight: bold;
    }

    &__infoContainer {
        box-sizing: border-box;
        height: calc(100vh - 200px - 120px - 60px);
        margin: 60px 60px;
        width: 400px;
    }

    &__info {
        display: flex;
        flex-direction: column;
        max-height: 100%;
        overflow-y: auto;
    }

    &__name {
        font-size: $font-size-large;
        margin-top: 0;
        margin-bottom: 60px;
    }

    &__general {
        font-size: $font-size-normal;
        margin-top: 0;
    }

    &__actions {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 20px;
    }

    &__links {
        height: 40px;
    }

    &__link {
        display: inline-flex;
        width: 40px;
        height: 100%;
        font-size: 40px;
        margin-right: 20px;

        &:last-child {
            margin-right: 0;
        }
    }

    &__moreInfo {
        display: flex;
        align-items: center;
        height: 40px;
        padding: 20px;
    }
}