.button {
    // display: inline-flex;
    text-decoration: none;
    color: $color-dark;
    background-color: $color-primary;
    font-size: $font-size-medium;
    font-weight: bold;
    padding: 4px 10px;

    transition: background-color 200ms ease-out;

    &:hover {
        background-color: $color-white;
    }
}