.switch {
    &__container {
        position: relative;
        display: flex;
        align-items: center;
        background-color: $color-dark-lighter;
        width: fit-content;
        height: 30px;
        font-weight: bold;
        font-size: $font-size-medium;

        cursor: pointer;
        user-select: none;
    }

    &__handle {
        position: absolute;
        height: 100%;
        background-color: $color-primary;

        transition: width 300ms ease-out, margin 300ms ease-out;
    }

    &__label {
        width: fit-content;
        padding: 0 20px;
        z-index: 2;
        color: $color-primary;
        transition: color 300ms ease-out;

        &--active {
            color: $color-dark;
        }
    }
}